<template>
	<view class="page">
		<statusHeader v-show="list.length > 0" ref="statusHeader" @statusClick="statusClick"></statusHeader>
		<view v-if="!isGet">
			<view class="bgwhite" style="margin-top: 10px">
				<mescroll-uni v-if="list.length > 0" ref="mescrollRef" @init="mescrollInit" :top="'62px'" :topbar="false"
					:fixed="true" @down="downCallback" @up="upCallback" :down="downOption" :up="upOption">
					<view class="oee-wrap bgwhite">
						<view class="oee-list ch-flex" v-for="item in list" :key="item.id" @click="itemClick(item)">
							<view class="ch-flex-1">
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('机器编号') }}：</view>
									<view class="ch-flex-1 value">{{ item['机器编号'] }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('工单号') }}：</view>
									<view class="ch-flex-1 value">{{ item['工单号'] }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('工作中心') }}： </view>
									<view class="ch-flex-1 value">{{ item['工作中心'] }}</view>
								</view>
								<view class="ch-flex">
									<view class="label nowwidth">{{ $t('表现性') }}： </view>
									<view class="ch-flex-1 value">{{ Number(item['表现性'] * 100).toFixed(1) }}%</view>
								</view>
								<view class="ch-flex">
									<view class="label">OEE：</view>
									<view class="ch-flex-1 value">{{ Number(item['OEE'] * 100).toFixed(1)}}%</view>
								</view>
							</view>
							<view class="rightWrap">
								<u-icon name="arrow-right" size="18" color="#bfbfbf"></u-icon>
							</view>
						</view>
					</view>
				</mescroll-uni>
				<view class="emptyView" v-else>
					<u-empty :text="$t('数据为空')" mode="list"></u-empty>
				</view>
			</view>
		</view>
		<template v-else>
			<u-loading-icon text="loading"></u-loading-icon>
		</template>
	</view>
</template>

<script>
import listScroll from "@/mixins/listScroll";
import statusHeader from "@/components/status-header/status-header"
import Api from "@/api/oee/index.js"
import deviceApi from "@/api/device/index.js"
export default {
	mixins: [listScroll],
	components: {
		statusHeader
	},
	onLoad() {

	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("oee.monitoring")
		});
	},
	data() {
		return {
			apiUrl: Api.list,
			method: 'get',
			query: {},
		};
	},
	methods: {
		//请求后处理
		afterGetList() {

			//获取设备状态
			uni.$u.http.get(deviceApi.list).then(result => {
				console.log('result', result);
				if (result.code === 200) {
					this.$refs["statusHeader"].handleStateList(result.data)
				}
			}).catch(er => {
				console.log('er', er);
			}).finally(() => {

			})
		},

		statusClick(state) {

		},

		//跳转详情
		itemClick(item) {
			uni.navigateTo({
				url: "/pages/machine/oeeDetail?detail=" + JSON.stringify(item.detail),
			})
		},
	}
}
</script>

<style lang="scss" scoped>
.oee-wrap {
	.oee-list {
		@include after();
		padding: 22upx 24upx;
		align-items: center;

		.ch-flex-1 {
			font-size: 28upx;

			.ch-flex {
				margin-bottom: 6upx;
			}
		}

		.label {
			width: auto;
			color: #333;
		}

		.value {
			color: #5e5e5e;
		}
	}
}
</style>